import axios from 'axios';
import { authHeader } from './authservice/auth-header';

const API_URL = process.env.VUE_APP_BASE_API_URL;

class ArticleService {
    getArticle() {
        return axios.get(API_URL+'articles', { headers: authHeader() });
    }
    addArticle(data){
        return axios.post(API_URL+'articles', data, { headers: authHeader() });
    }
    editArticle(id) {
        return axios.get(API_URL+'articles/'+id, { headers: authHeader() });
    }
    updateArticle(id, data) {
        return axios.post(API_URL+'articles/update/'+id, data, { headers: authHeader() });
    }
    deleteArticle(id) {
        return axios.delete(API_URL+'articles/'+id, { headers: authHeader() });
    }
    bulkdeleteArticle(data) {
        return axios.post(API_URL +'articles/bulk-delete', data, { headers: authHeader() });
    }
        
}

export default new ArticleService();
