                              <b-button size="md" variant="success" @click="addArticles">
                                {{$t('button.save.text')}}
                              </b-button>
                            </template>
                      </b-modal> -->

                      <b-dropdown class="btn-group mr-2 mb-2 mb-sm-0 border rounded" variant="white"  ref="dropdown">
                        <template slot="button-content">
                          {{ $t('table.show-label') }}
                          <i class="mdi mdi-chevron-down ml-2"></i>
                        </template>
                        <span v-for="val in pageOptions" :key="val">
                            <button class="dropdown-item" @click="perPageSelect(val)">
                             <span class="text-success" v-if="val == perPage"><b> {{val}} </b></span>
                             <span v-else>{{val}}</span>
                            </button>
                        </span>
                      </b-dropdown>
                      <b-dropdown class="btn-group mr-2 mb-2 mb-sm-0 border" variant="white" v-if="selected[0]">
                        <template slot="button-content">
                          {{ $t('button.bulk-action') }}
                          <i class="mdi mdi-chevron-down ml-2"></i>
                        </template>
                        <button class="dropdown-item" @click="bulkAction()">{{ $t('button.hapus.text') }}</button>
                      </b-dropdown>
                  </div>
              </div>
              <div class="col-4 col-xl-3">
              </div>
                <div class="col-3 col-xl-2">
                  <div class="form-group has-search">
                    <span class="bx bx-search-alt search-icon form-control-feedback"></span>
                      <b-form-input
                      v-model="filter"
                      type="search"
                      :placeholder="$t('navbar.search.text')"
                      class="form-control"
                    ></b-form-input>
                  </div>
              </div>
              <!-- end col-->
             </div>
             </div>

            <!-- Table -->
            <div class="table-responsive mb-0" :class="{ 'pb-5' : rows < 10 }" v-if="user_type == 'su' || user_type == 'adm'">
              <div v-if="!loading">
                <b-table
                :items="tableData"
                :fields="fields"
                bordered
                striped
                responsive="sm"
                :per-page="perPage"
                :current-page="currentPage"
                :sort-desc.sync="sortDesc"
                :filter="filter"
                :filter-included-fields="filterOn"
                @filtered="onFiltered"
              >
                <template #head(bulk) width="4%">
                    <b-form-checkbox
                            v-model="selectAll" 
                            @change="select"
                            class="text-center mt-0">
                    </b-form-checkbox>
                </template>
                <template #cell(bulk)="row" width="4%">
                <b-form-checkbox
                  class="text-center mt-0"
                  v-model="selected"
                  :value="row.item.id_article"
                >
                </b-form-checkbox>
                </template>
                <template #cell(article_pict)="row" width="4%">
                    <div>
                      <img :src="urlImage+'article/'+row.item.article_pict" :alt="row.item.article_pict" class="img-fluid w-100">
                    </div>
                </template>

                  <template #cell(article_desc)="row" width="4%">
                    <div>
                       <p class="pl-2 pr-4 pt-0 pb-0" v-html="row.item.article_desc"></p>
                    </div>
                </template>

                <template #cell(actions)="row">
                      <router-link :to="'/article-edit/'+row.item.id_article">
                        <b-button class="btn btn-sm mr-1" variant="info"><i class="bx bx-pencil"></i> 
                        </b-button>
                      </router-link>
                       <!-- <b-modal
                        ref="ref-modal-edit"
                        :id="'modal-edit' + row.item.id_article"
                        centered
                        :title="$t('button.update.text')"
                        title-class="font-17"
                        :ok-title="$t('button.update.text')"
                        :cancel-title="$t('button.cancel.text')"
                      >
                          <div class="row">
                            <div class="col-md-12">
                              <div class="form-group">
                                <label for="validationCustom01">{{
                                  $t("article.title")
                                }}</label>
                                <input
                                  id="validationCustom01"
                                  type="text"
                                  class="form-control"
                                  :placeholder="
                                    $t('article.title')
                                  "
                                  v-model="form.article_title"
                                  :class="{
                                    'is-invalid': submitted && $v.form.article_title.$error,
                                  }"
                                />
                                <div
                                  v-if="submitted && $v.form.article_title.$error"
                                  class="invalid-feedback"
                                >
                                  <span v-if="!$v.form.article_title.required">{{
                                    $t("form.error.text")
                                  }}</span>
                                </div>
                              </div>
                            </div>   
                             <div class="col-md-12">
                              <div class="form-group">
                                <label for="validationCustom02">{{
                                  $t("article.desc")
                                }}</label>
                                <input
                                  id="validationCustom01"
                                  type="text"
                                  class="form-control"
                                  :placeholder="
                                    $t('article.desc')
                                  "
                                  v-model="form.article_desc"
                                  :class="{
                                    'is-invalid': submitted && $v.form.article_desc.$error,
                                  }"
                                />
                                <div
                                  v-if="submitted && $v.form.article_desc.$error"
                                  class="invalid-feedback"
                                >
                                  <span v-if="!$v.form.article_desc.required">{{
                                    $t("form.error.text")
                                  }}</span>
                                </div>
                              </div>
                            </div> 
                              <div class="col-md-12">
                              <div class="form-group">
                                <label for="validationCustom01">{{
                                  $t("article.image")
                                }}</label>

                                  <b-form-file
                                    :state="true"
                                    :placeholder="$t('user-management.choose')"
                                    :drop-placeholder="$t('user-management.drop')"
                                    @change="ImageEditTarget"
                                    accept="image/*"
                                    :class="{
                                  }"
                                >
                                </b-form-file>
                              
                              </div>
                            </div> 
                          </div>
                          <template #modal-footer="{ cancel }">
                              <b-button size="md" variant="danger" @click="cancel()">
                                {{$t('button.cancel.text')}}
                              </b-button>
                              <b-button size="md" variant="success" @click="updateArticles($event, row.item.id_article)">
                                {{$t('button.save.text')}}
                              </b-button>
                            </template>
                      </b-modal> -->
                      <b-button class="btn btn-sm" variant="danger" @click="deletearticle(row.item.id_article)"><i class="bx bx-trash"></i> 
                      </b-button>
                </template>
                </b-table>
              </div>
              <div v-if="!tableData[0] && !loading">
                <div  class="row  mt-5 mb-5 pb-4 mx-auto">
                    <div class="col-12  p-5 my-5 text-center">
                        <span class="text-secondary">{{ $t('all.dnf') }}</span>  
                    </div>
                </div>
              </div>
            </div>
            <div class="row" v-if="rows > 10">
               <div class="col-6 text-left">
                <div class="text-muted float-left">
                    {{ $t('table.show-label') }} <b>{{perPage}} </b>{{ $t('table.entri-label') }}
                </div>
              </div>
              <div class="col-6 text-right">
                <div class="dataTables_paginate paging_simple_numbers float-right">
                  <ul class="pagination pagination-rounded mb-0">
                    <!-- pagination -->
                    <b-pagination
                      v-model="currentPage"
                      :total-rows="rows"
                      :per-page="perPage"
                    ></b-pagination>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
