</template>

<script>
import Vue from "vue";
import Swal from "sweetalert2";
import Layout from "../../layouts/main";
import PageHeader from "@/components/page-header";
import appConfig from "@/app.config";
import { required, sameAs } from "vuelidate/lib/validators";
import UserService from "@/helpers/user.service";
import ArticleService from "@/helpers/article.service";
import store from '@/state/store';
import moment from "moment";

export default {
  page: {
    meta: [{ name: "description", content: appConfig.description }],
  },
  components: { Layout, PageHeader },
  data() {
    return {
      urlImage: process.env.VUE_APP_BASE_API_URL_IMAGE,
      user_type: store.state.authBackend.user.type,
      title: this.$t("article.article-management"),
      items: [
        {
          text: this.$t("menuitems.dashboards.text"),
          href: "#",
        },
        {
          text: this.$t("article.article-management"),
          active: true,
        },
      ],
      loading:false,
      tableData:[],
      totalRows: 1,
      currentPage: 1,
      perPage: 10,
      pageOptions: [10, 25, 50, 100],
      filter: null,
      filterOn: [],
      sortDesc: false,
      selected:[],
      selectAll: false,
      fields: [
        { key: "id_article", thClass: "d-none", tdClass: "d-none" },
        { key: "bulk",  thStyle: { width: "2%" }, label:'#'},
          {
          key: "article_pict",  
          thStyle: { width: "20%" },
          sortable: true,
          label: this.$t("article.image"),
        },
        {
          key: "article_title",  
          thStyle: { width: "25%" },
          sortable: true,
          label: this.$t("article.title"),
        },
        {
          key: "article_desc",
          sortable: true,
          label: this.$t("article.desc"),
        },
        // { 
        //   key: "created_at", 
        //   thStyle: { width: "15%" }, 
        //   sortable: true,
        //   label: this.$t("all.created-at"),
        // },
        {
          key: "actions", 
          thStyle: { width: "12%" },
          class: "text-center",
          label: this.$t("button.action"),
        },
      ],
      form: {
          "article_desc" : "",
          "article_title" : "",
          "article_pict" : "",

      },
      file:{
        "article_pict": '',
        "name": ''
      },
      submitted: false,
    };
  },
  validations: {
    form: {
        article_desc: { required },
        article_title: { required },
        article_pict: { required }
    },
  },
  computed: {
    rows() {
      return this.tableData.length;
    },
  },
  methods: {
    select() {
        this.selected = [];
        if (!this.selectAll) {
            for (let i in this.tableData) {
                this.selected.push(this.tableData[i].id_article);
            }
        }
    },

    list() {
      // this.loading = true;
      UserService.getProfile()
      .then(response => {
        // console.log( response.data.data)
        this.user_type = response.data.data.admin.role
      })
      .catch(e => {
        this.message = e.response.data.data
      });
    },

    ImageTarget(evt){
      this.form.article_pict = evt.target.files[0];
    },

    ImageEditTarget(evt){
      this.file.article_pict = evt.target.files[0];
      this.file.name = evt.target.files[0].name;
    },

    getArticles() {
      this.loading = true;
      ArticleService.getArticle()
        .then((response) => {
          this.tableData = [];
          // console.log(response.data.data);
          response.data.data.forEach((val) => {
            const data = {
              id_article: val.id_article,
              article_pict: val.article_pict,
              article_desc: val.article_desc,
              article_title: val.article_title,
              created_at: this.formatDate(val.created_at),
              updated_at: this.formatDate(val.updated_at),
            };
            this.tableData.push(data);
            this.loading = false;
          });
        })
        .catch((err) => {
          this.loading = false;
          console.log(err);
        });
    },

    getArticlesUpdate() {
      ArticleService.getArticle()
        .then((response) => {
          this.tableData = [];
          console.log(response.data.data);
          response.data.data.forEach((val) => {
            const data = {
              id_article: val.id_article,
              article_pict: val.article_pict,
              article_desc: val.article_desc,
              article_title: val.article_title,
              created_at: this.formatDate(val.created_at),
              updated_at: this.formatDate(val.updated_at),
            };
            this.tableData.push(data);
            this.loading = false;
          });
        })
        .catch((err) => {
          this.loading = false;
          console.log(err);
        });
    },

    addArticleModal(){
        this.$bvModal.show('modal-add')
        this.form.article_desc = "";
        this.form.article_title = "";
        this.form.article_pict = "";
    },

    addArticles(e) {
      // this.loadingAdd = true;
      this.submitted = true;
      this.$v.form.$touch();
      if (
        this.form.article_desc == ""  || 
        this.form.article_title == "" || 
        this.form.article_pict == ""
      ) {
        e.preventDefault();
      } else {
        const formImages = new FormData();
        formImages.append('article_title', this.form.article_title)
        formImages.append('article_desc', this.form.article_desc)
        formImages.append('article_pict', this.form.article_pict, this.form.article_pict.name);
        console.log(formImages);
        ArticleService.addArticle(formImages)
          .then((response) => {
            Vue.swal({
              position: "top-end",
              icon: "success",
              title: response.data.message,
              showConfirmButton: false,
              timer: 1500,
            });
            this.resetForm();
            this.$v.$reset();
            this.$bvModal.hide('modal-add');
            this.getArticlesUpdate();
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },

    editBannner(id_article, article_desc, article_title) {
      // console.log(id_article);
      this.$bvModal.show("modal-edit" + id_article);
      this.form.article_desc = article_desc;
      this.form.article_title = article_title;
    },

    updateArticles(e, id) {
      this.submitted = true;
      this.$v.form.$touch();
      if ( this.form.article_desc == "" || this.form.article_title == "") {
        e.preventDefault();
      } else {
        const formImages = new FormData();
        formImages.append('article_title', this.form.article_title)
        formImages.append('article_desc', this.form.article_desc)
        if(this.file.article_pict !== ''){
            formImages.append('article_pict', this.file.article_pict)
        }
        // console.log(id, this.file.article_pict);
        ArticleService.updateArticle(id, formImages)
          .then((response) => {
            let id_article = response.data.data.id_article;
            Vue.swal({
              position: "top-end",
              icon: "success",
              title: response.data.message,
              showConfirmButton: false,
              timer: 1500,
            });
            this.$bvModal.hide("modal-edit" + id_article);
            this.getArticlesUpdate();
            this.file = {
                        "article_pict": "",
                        "name":""
                      };
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    
    deletearticle(id) {
      Swal.fire({
        title: this.$t("button.delete.modal.confirmText[0]"),
        text: this.$t("button.delete.modal.confirmText[1]"),
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#34c38f",
        cancelButtonColor: "#f46a6a",
        cancelButtonText: this.$t("button.cancel.text"),
        confirmButtonText: this.$t("button.delete.text"),
      }).then((result) => {
        if (result.isConfirmed) {
          // console.log(id);
          ArticleService.deleteArticle(id)
            .then((response) => {
              this.getArticlesUpdate();
              Swal.fire({
                position: "top-end",
                icon: "success",
                title:  response.data.message,
                showConfirmButton: false,
                timer: 1500,
              });
            })
            .catch((err) => {
              console.log(err);
            });
        }
      });
    },

     bulkAction(){
          Swal.fire({
              title: this.$t('button.delete.modal.confirmText[0]'),
              text: this.$t('button.delete.modal.confirmText[1]'),
              icon: "warning",
              showCancelButton: true,
              confirmButtonColor: "#34c38f",
              cancelButtonColor: "#f46a6a",
              cancelButtonText: this.$t('button.cancel.text'),
              confirmButtonText: this.$t('button.delete.text')
          }).then((result) => {
              if (result.isConfirmed) {
                var data = this.selected;
                console.log(data);
                  ArticleService.bulkdeleteArticle(data)
                  .then( response => {
                      this.selected = [];
                      this.getArticlesUpdate();
                      Swal.fire({
                          position: "top-end",
                          icon: "success",
                          title:  response.data.message,
                          showConfirmButton: false,
                          timer: 1500
                      });
                  })
                  .catch( err => {
                      console.log(err);
                  });
              }
          });
    },
  
    onFiltered(filteredItems) {
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    },
    onDelete(id) {
      console.log("delete data:", id);
    },
    onView(id) {
      console.log("details data:", id);
    },
    perPageSelect(val){
      this.perPage = val;
      console.log(this.perPage);
      this.$refs.dropdown.hide();
    },
    resetForm() {
      setTimeout(() => {
        this.form.article_desc = "",
        this.form.article_title = ""
      }, 2000);
    },
    formatDate(value) {
      return moment(value).format("YYYY-MM-DD, hh:mm");
    },
  },
  beforeMount(){
    this.list();
  },
  mounted() {
    this.totalRows = this.items.length;
    this.getArticles();
  },
};
</script>

<style scoped>
.badge {
  border-radius: 2%;
}
.img-fluid{
  height: 100px;
}

</style>
